import styled from 'styled-components';
import { colors, mobileThresholdPixels } from '../styledComponents';
import trait from '../../assets/trait.png';

export const Row = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin: 10px 0px;
`;

export const TextLink = styled.a`
  color: ${colors.navy};
  text-decoration: none;

  &:hover {
    transform: scale(1.1);
  }

  @media (max-width: ${mobileThresholdPixels}) {
    font-size: 10px;
    text-align: center;
  }
`;

export const Content = styled.div`
  width: 100%;
  background-color: white;
`;

export const Container = styled.div`
  display: flex;
  justify-content: center;
  overflow-x: hidden;
  background-color: white;
`;

const headerPhotoMargin = 40;
export const HeaderPhoto = styled.img`
  margin: 0px ${headerPhotoMargin}px;
  object-fit: cover;
  height: 800px;
  width: calc(100vw - ${2 * headerPhotoMargin}px);

  @media (max-width: ${mobileThresholdPixels}) {
    margin: 0px;
    height: auto;
    width: 100vw;
    object-fit: unset;
  }
`;

export const Button = styled.div`
  display: inline-block;
  zoom: 1;
  min-width: 220px;
  text-transform: uppercase;
  background: 0;
  border: 2px solid ${colors.navy};
  cursor: pointer;
  font-weight: bold;
  font-size: 18px;
  margin: 20px auto;
  padding: 20px 25px;
  letter-spacing: 1px;
  -webkit-transition: all .1s cubic-bezier(.7,0,.3,1);
  transition: all .1s cubic-bezier(.7,0,.3,1);
  color: ${colors.navy}
  background-color: #fff;
  transition: all 0.3s ease;
  opacity: 0.9;

  &:hover {
    opacity: 1;
    transform: scale(1.1);
  }

  @media (max-width: ${mobileThresholdPixels}) {
    padding: 20px 25px;
    font-size: 14px;
    margin-top: 15px;
  }
`;

export const ButtonHeaderContainer = styled.div`
  position: absolute;
  bottom: 100px;
  width: 100%;
  text-align: center;

  @media (max-width: ${mobileThresholdPixels}) {
    position: static;
  }
`;

export const Title2 = styled.h2`
  text-align: center;
  width: 100%;
  color: ${colors.navy};
  font-size: ${props => props.small ? '36px' : '48px'};
  font-weight: 300;

  @media (max-width: ${mobileThresholdPixels}) {
    font-size: ${props => props.small ? '18px' : '20px'};
  }
`;

export const Title3 = styled.h3`
  text-align: center;
  width: 100%;
  color: ${colors.navy};
  font-size: 24px;
  font-weight: 300;
  margin: 0px;

  @media (max-width: ${mobileThresholdPixels}) {
    font-size: 20px;
    margin-top: 20px;
  }
`;

export const Banner = styled.div`
  background-image: url(${trait});
  background-repeat: repeat-x;
  height: 60px;
  width: ${props => props.width || '100vw'};
`;

export const Icon = styled.img`
  height: 80px;
  width: 80px;
  margin: 20px;

  &:hover{
    transform: scale(1.1);
  }

  @media (max-width: ${mobileThresholdPixels}) {
    margin: 10px;
  }
`;



